import React from 'react';
import PropTypes from 'prop-types';
import { makeStyles, withStyles } from '@material-ui/core/styles';
import clsx from 'clsx';
import Stepper from '@material-ui/core/Stepper';
import Step from '@material-ui/core/Step';
import StepLabel from '@material-ui/core/StepLabel';
import Check from '@material-ui/icons/Check';
import DescriptionIcon from '@material-ui/icons/Description';
import TapAndPlayIcon from '@material-ui/icons/TapAndPlay';
import FingerprintIcon from '@material-ui/icons/Fingerprint';
import StepConnector from '@material-ui/core/StepConnector';
import Button from '@material-ui/core/Button';
import Typography from '@material-ui/core/Typography';
import axios from 'axios';
import { URI } from '../../App';

const QontoConnector = withStyles({
  alternativeLabel: {
    top: 10,
    left: 'calc(-50% + 16px)',
    right: 'calc(50% + 16px)',
  },
  active: {
    '& $line': {
      borderColor: '#784af4',
    },
  },
  completed: {
    '& $line': {
      borderColor: '#784af4',
    },
  },
  line: {
    borderColor: '#eaeaf0',
    borderTopWidth: 3,
    borderRadius: 1,
  },
})(StepConnector);

const useQontoStepIconStyles = makeStyles({
  root: {
    color: '#eaeaf0',
    display: 'flex',
    height: 22,
    alignItems: 'center',
    borderRadius: '1.5rem'
  },
  active: {
    color: '#784af4',
  },
  circle: {
    width: 8,
    height: 8,
    borderRadius: '50%',
    backgroundColor: 'currentColor',
  },
  completed: {
    color: '#784af4',
    zIndex: 1,
    fontSize: 18,
  },
});

function QontoStepIcon(props) {
  const classes = useQontoStepIconStyles();
  const { active, completed } = props;

  return (
    <div
      className={clsx(classes.root, {
        [classes.active]: active,
      })}
    >
      {completed ? <Check className={classes.completed} /> : <div className={classes.circle} />}
    </div>
  );
}

QontoStepIcon.propTypes = {
  /**
   * Whether this step is active.
   */
  active: PropTypes.bool,
  /**
   * Mark the step as completed. Is passed to child components.
   */
  completed: PropTypes.bool,
};

const ColorlibConnector = withStyles({
  alternativeLabel: {
    top: 22,
  },
  active: {
    '& $line': {
      backgroundImage:
        'linear-gradient( 95deg,rgb(242,113,33) 0%,rgb(233,64,87) 50%,rgb(138,35,135) 100%)',
    },
  },
  completed: {
    '& $line': {
      backgroundImage:
        'linear-gradient( 95deg,rgb(242,113,33) 0%,rgb(233,64,87) 50%,rgb(138,35,135) 100%)',
    },
  },
  line: {
    height: 3,
    border: 0,
    backgroundColor: '#eaeaf0',
    borderRadius: 1,
  },
})(StepConnector);

const useColorlibStepIconStyles = makeStyles({
  root: {
    backgroundColor: '#ccc',
    zIndex: 1,
    color: '#fff',
    width: 50,
    height: 50,
    display: 'flex',
    borderRadius: '50%',
    justifyContent: 'center',
    alignItems: 'center',
  },
  active: {
    backgroundImage:
      'linear-gradient( 136deg, rgb(242,113,33) 0%, rgb(233,64,87) 50%, rgb(138,35,135) 100%)',
    boxShadow: '0 4px 10px 0 rgba(0,0,0,.25)',
  },
  completed: {
    backgroundImage:
      'linear-gradient( 136deg, rgb(242,113,33) 0%, rgb(233,64,87) 50%, rgb(138,35,135) 100%)',
  },
});

function ColorlibStepIcon(props) {
  const classes = useColorlibStepIconStyles();
  const { active, completed } = props;

  const icons = {
    1: <DescriptionIcon />,
    2: <TapAndPlayIcon />,
    3: <FingerprintIcon />,
  };

  return (
    <div
      className={clsx(classes.root, {
        [classes.active]: active,
        [classes.completed]: completed,
      })}
    >
      {icons[String(props.icon)]}
    </div>
  );
}

ColorlibStepIcon.propTypes = {
  /**
   * Whether this step is active.
   */
  active: PropTypes.bool,
  /**
   * Mark the step as completed. Is passed to child components.
   */
  completed: PropTypes.bool,
  /**
   * The label displayed in the step icon.
   */
  icon: PropTypes.node,
};

const useStyles = makeStyles((theme) => ({
  root: {
    width: '100%',
  },
  button: {
    marginRight: theme.spacing(1),
  },
  instructions: {
    marginTop: theme.spacing(1),
    marginBottom: theme.spacing(1),
  },
}));

function getSteps() {
  return ['Check Required document', 'Scan your Licence', 'Scan your Figurprint'];
}

function getStepContent(step) {
  switch (step) {
    case 0:
      return 'Check if You have all the required Document';
    case 1:
      return 'Scan your licnece through NFC or App';
    case 2:
      return 'Scan your Fingurprint throug Scanner';
    default:
      return 'Unknown step';
  }
}

export default function CustomizedSteppers(props) {
  const classes = useStyles();
  const [activeStep, setActiveStep] = React.useState(0);
  const [user , setUser] = React.useState({})
  const steps = getSteps();

  const moddy = ()=>{
    document.getElementById("modal-boss")?.classList.toggle("is-active")
  }
  const moddyClose = ()=>{
    document.getElementById("modal-boss")?.classList.remove("is-active")
  }
  const handleNext = () => {

    if(activeStep===0){
      axios.get(URI+"/verify_documents").then(
        data=>{
          console.log(data);
          if(data?.data =="Success"){
            props.changeMessage({head:"Document Check",type:"is-success",msg:"ðŸ‘ all documents available"})
            moddy()        
            setActiveStep((prevActiveStep) => prevActiveStep + 1);
            setTimeout(()=>{
              moddyClose()
            },3000)
          } else {
            props.changeMessage({head:"Oh no",type:"is-danger",msg:data.data})
            moddy()        
            setTimeout(()=>{
              moddyClose()
            },3000)
          }
        }
      )
    } 
    else if(activeStep===1){
      axios.get(URI+"/verify_license").then(
        data=>{
          console.log(data);
          if(data.data.length >0 ){
            console.log(data.data[0]);
            setUser(data.data[0])
            props.changeMessage({head:"ðŸ’³ License",type:"is-success",msg:"ðŸ‘hi! "+data.data[0].name+" we Found your license"})
            moddy()        
            setActiveStep((prevActiveStep) => prevActiveStep + 1);
            setTimeout(()=>{
              moddyClose()
            },3000)
          } else {
            props.changeMessage({head:"Oh no",type:"is-danger",msg:"License didnt match"})
            moddy()        
            setTimeout(()=>{
              moddyClose()
            },3000)
          }
        }
      )
    }
    else if(activeStep===2 ){
      axios.post(URI+"/verify_fingerprint",{fingerprint:user.fingerprint}).then(
        data=>{
          console.log(data);
          if(data.data == 1 ){
         
            props.changeMessage({head:"ðŸ§¬Fingerprint",type:"is-success",msg:"ðŸ‘Fingerprint Matched \n \n you can ride your vehicle safely"})
            moddy()        
            setActiveStep((prevActiveStep) => prevActiveStep + 1);
            setTimeout(()=>{
              moddyClose()
            },3000)
          } else {
            props.changeMessage({head:"Oh no",type:"is-danger",msg:"Fingerprint didnt match!!Try again"})
            moddy()        
            setTimeout(()=>{
              moddyClose()
            },3000)
          }
        }
      )
    }
    
  };

  const handleBack = () => {
    setActiveStep((prevActiveStep) => prevActiveStep - 1);
  };

  const handleReset = () => {
    setActiveStep(0);
  };

  return (
    <div className={classes.root}>
      <Stepper alternativeLabel activeStep={activeStep} connector={<ColorlibConnector />}>
        {steps.map((label) => (
          <Step key={label}>
            <StepLabel StepIconComponent={ColorlibStepIcon}>{label}</StepLabel>
          </Step>
        ))}
      </Stepper>
      <div>
        {activeStep === steps.length ? (
          <div>
            <Typography className={classes.instructions} style={{color:"white"}}>
              All steps completed - you&apos;re finished! enjoy your ride
            </Typography>
            <Button onClick={handleReset} className={classes.button} style={{color:"white"}}>
              Reset
            </Button>
          </div>
        ) : (
          <div>
            <Typography  style={{color:"white"}} className={classes.instructions}>{getStepContent(activeStep)}</Typography>
            <div>
              <Button disabled={activeStep === 0} onClick={handleBack} className={classes.button} style={{color:"white"}}>
                Back
              </Button>
              <Button
                variant="contained"
                color="primary"
                onClick={handleNext}
                className={classes.button}
              >
                {activeStep === steps.length - 1 ? 'Ride' : 'Verify'}
              </Button>
            </div>
          </div>
        )}
      </div>
    </div>
  );
}