import React, { useState } from 'react';
import {
  fade,
  ThemeProvider,
  withStyles,
  makeStyles,
  createMuiTheme,
} from '@material-ui/core/styles';
import InputBase from '@material-ui/core/InputBase';
import InputLabel from '@material-ui/core/InputLabel';
import TextField from '@material-ui/core/TextField';
import FormControl from '@material-ui/core/FormControl';
import { green } from '@material-ui/core/colors';
import CloudUploadIcon from '@material-ui/icons/CloudUpload';
import Button from '@material-ui/core/Button';
import Typography from '@material-ui/core/Typography';
import axios from 'axios';
import { URI } from '../../App';




const useStyles = makeStyles((theme) => ({
  root: {
    display: 'flex',
    flexWrap: 'wrap',
    color:"#ffffff",
  },
  margin: {
    margin: theme.spacing(1),
    color:"#ffffff"
  },
}));

const ValidationTextField = withStyles({
    root: {
      '& input:valid + fieldset': {
        borderColor: 'green',
        borderWidth: 2,
        color:"#ffffff",
      },
      
      '& input:invalid + fieldset': {
        borderColor: 'red',
        borderWidth: 2,
        color:"#ffffff",
      },
      '& input:valid:focus + fieldset': {
        borderLeftWidth: 6,
        color:"#ffffff",
        padding: '4px !important', // override inline-style
      },
    },
  })(TextField);



export default function CustomizedInputs(props) {
  const classes = useStyles();
  const [ins , insSet] = useState("")
  const moddy = ()=>{
    document.getElementById("modal-boss")?.classList.toggle("is-active")
  }
  const moddyClose = ()=>{
    document.getElementById("modal-boss")?.classList.remove("is-active")
  }
  const addPUC = ()=>{
    axios.post(URI+"/add_document",{type:"PUC",id:ins}).then(
      data => {
        if(data.data!=="Bad request"){
          props.changeModal({head:"PUC",type:"is-success",msg:"Document added succesfully"})
        }
        else {
          props.changeModal({head:"PUC",type:"is-danger",msg:data.data})
        }
        moddy()        
            setTimeout(()=>{
              moddyClose()
            },3000)
      }
    )
  }
  return (
    <form >
      <ValidationTextField
        label="PUC Number"
        required
        variant="outlined"
        defaultValue="Licence No"
        color='secondary'
        id="validation-outlined-input"
        style={{padding:10}}
        onChange={e=>insSet(e.target.value)}
      />
      
      <Button
        variant="contained"
        color="default"
        style={{background: "linear-gradient(45deg, #13B3C8  40%, #FFFFFF 70%)",paddingTop:10}}
        startIcon={<CloudUploadIcon />}
        onClick={()=>{addPUC()}}
      >
        Upload
      </Button>
    </form>
  );
}
