import React, { Component } from "react";
import {Link} from 'react-router-dom'
import './landing.css';
import Bugatti from './bugatti.png';
import axios from "axios";
import { URI } from "../App";

export default class Landing extends Component {

    constructor(){
        super()
        this.state={
            "docs":[]
        }
    }
    drawMeter(id , perc){
        let puc = document.getElementById(id);
        let pucCtx = puc.getContext("2d");
        let gradient = pucCtx.createLinearGradient(0, 0,290, 20);
        gradient.addColorStop("0", "red");
        gradient.addColorStop("0.5" ,"yellow");
        gradient.addColorStop("1.0", "green");

        let angle = (1+perc) * Math.PI
        let x1 = 150;
        let y1 = 110;
        let length =  100;

        let x2 = x1 + Math.cos(angle) * length;
        let y2 = y1 + Math.sin(angle) * length;

        pucCtx.strokeStyle="#FF0000";
        pucCtx.lineWidth = 5;
        pucCtx.moveTo(x1, y1);
        pucCtx.lineTo(x2, y2);
        pucCtx.stroke();

        //empty bar
        pucCtx.strokeStyle = "#808080";
        pucCtx.lineWidth = 12;
        pucCtx.beginPath();
        pucCtx.arc(150, 110, 100, 1*Math.PI, 2* Math.PI);
        pucCtx.stroke();

        

        // Fill with gradient
        pucCtx.strokeStyle = gradient;
        pucCtx.lineWidth = 10;
        pucCtx.beginPath();
        pucCtx.arc(150, 110, 100, 1*Math.PI, (1+perc )* Math.PI);
        pucCtx.stroke();

        

        
        
    }

    getAllDocs(){
        axios.get(URI+"/get_all_docs").then(
            data =>{
                this.setState({'docs':data.data.docs})
                this.state.docs.forEach((a)=>{
                    if(a.docname=="PUC"){
                        this.setState({ "puc":a.valid_till ,...this.state})
                    }
                    if(a.docname=="INS"){
                        this.setState({ "ins":a.valid_till ,...this.state})
                    }
                })
                console.log(this.state);
            }
        )
    }
    componentDidMount(){

        this.getAllDocs()
        console.log(this.state);
        // set PUC progress bar
        this.drawMeter("puc" , 0.4)



        // for inurance  coverage

        this.drawMeter("ic",0.7)
    }


    render(){
        return (
            <div className="container">
                <div className="overlay">
                  <div className="slider">
                  ^<br/>^
                  </div>
                  <div className="unlock-text">
                  <Link to="/home">UNLOCK</Link>
                  </div>
                </div>
                <div class="logo ">
                Virtuoso
                </div>
               {/* <div className="lock disco-text">
                   UNlock
                   </div>  */}
             <div className="meter-1 disco-text">
                 {this.state.puc} <br/>PUC
                 <canvas id="puc"></canvas>
             </div>
             <div className="meter-2 disco-text">
                {this.state.ins} <br/> Insurance
                <canvas id="ic"></canvas>
             </div>
             <div className="car">
                    <img src={Bugatti} alt="car" id="bugatti"/>
            </div>
            <div className="infobar  disco-text">
            {this.state.docs.filter(a=>a.docname!=="test").map((el,idx)=>     <div>{el.docname} âœ…</div>
            )}
            {/* <div>Battery: ðŸ”‹87%</div>
            <div>Tour: ðŸš¦1475km</div>
            <div>State: âš¡Charging</div> */}
            </div>
            </div>
        )
    }
}