import { HashRouter as Router , Switch , Route, Redirect} from 'react-router-dom';
import Logs from './components/logs'
import Home from "./components/home";
import Drivers from "./components/drivers";
import Upload from "./components/upload";
import Upload2 from "./components/upload2"
import vehicledetails from './components/vehicledetails'
import Landing from './components/landing'
import { createMuiTheme } from '@material-ui/core/styles';

import {ThemeProvider} from '@material-ui/styles';

const theme = createMuiTheme({
  palette: {
    primary: {
      main: '#98acf8',
      backgroundColor: "black"
    },
    secondary: {
      main: '#f44336',
    },
  },
});


function App() {
  return (
    <div style={{backgroundColor: "black", minHeight:"100vh"}}>
    <ThemeProvider theme={theme} >
    <Router>
      {/* <Navbar></Navbar> */}
      <Switch>
        <Route path="/lock" component={Landing} exact/>
        <Redirect from="/" to="/lock" exact/>
      <Route  path="/home" component={Home}  exact/>
      <Route  path="/drivers" component={Drivers} exact />
      <Route  path="/logs" component={Logs} exact/>
      <Route path='/upload' component={Upload} exact/>
      <Route path='/upload2' component={Upload2} exact/>
      <Route  path="/vehicledetails" component={vehicledetails} exact/>       
      </Switch>
    </Router>
    </ThemeProvider>
    </div>
  );
}

export default App;

export const URI = "http://localhost:5000"